@media screen and (max-width: 480px) {
  .CeramicSizeBlock {
    min-width: 0;
    overflow: hidden;
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .CeramicSizeBlock h2 {
    min-width: 0 !important;
  }
  .CeramicBlocks {
    display: flex;
    flex-direction: column;
  }
  .CeramicBlocks .CeramicBlocksItem img {
    width: 100%;
  }
  .TwoTilesBlock_Items {
    grid-template-columns: 1fr;
    padding: 15px;
  }
  .CeramicAmountTile {
    display: grid;
    grid-template-columns: 1fr;
    flex-direction: column;
  }
  .main-page {
    max-width: 100% !important;
    min-width: auto;
    width: 100% !important;
  }
  .calculator-open {
    max-width: 100% !important;
  }
  .calculator_roomSize {
    grid-template-columns: 1fr !important;
    grid-column: 1 !important;
    padding: 0 !important;
    margin: 0;
    max-width: 370px;
  }
  .calculatorChooseComponent_item__img {
    height: 250px !important;
  }
  .header {
    max-width: 100% !important;
    min-width: auto !important;
  }
  .logoComponent {
    font-size: 12px !important;
  }
  .logoComponent p {
    text-align: left;
    line-height: 1.7;
    margin-left: 5px;
  }
  .calculator_tileSize {
    max-width: 100% !important;
    min-width: auto !important;
    grid-template-columns: 1fr !important;
    grid-template-rows: 100px repeat(8, 250px);
    width: 100% !important;
    margin: 0 !important;
  }
  .calculator_tileSize h2 {
    min-width: auto !important;
    max-width: 100%;
    grid-column: 1 !important;
  }
  .calculator_tileSize .calculator_tileSize__items {
    width: 80% !important;
    grid-column: 1 !important;
    height: 250px !important;
    grid-row: auto !important;
  }
  .calculatorBathMetres {
    width: 100%;
    grid-template-columns: 1fr;
    height: auto;
    min-width: auto;
    margin: 0 auto;
    max-width: 480px;
  }
  .calculatorBathMetres img {
    width: 100%;
    padding: 0 4px 4px 4px;
  }
  .calculatorBathMetres .calculatorBathMetres-mainPic {
    grid-gap: 5px;
    padding-bottom: 50px !important;
  }
  .calculatorBathMetres .calculatorBathMetres-mainPic .calculatorBathMetres-mainPic_inputField {
    width: 90% !important;
  }
  .calculatorBathMetres .calculatorBathMetres-mainPic h2 {
    padding-top: 0 !important;
    font-size: 30px !important;
    max-width: 700px !important;
  }
  .calculatorBath-additionalWork_opened__items {
    min-width: auto !important;
    max-width: 100% !important;
  }
  .AdditionalItemAngle {
    row-gap: 15px !important;
  }
  .footer {
    width: 100% !important;
  }
  .footer .footer_text {
    font-size: 11px;
  }
  .footer .footer-block {
    color: black !important;
  }
  .ListOfBenefitAndLimit {
    display: flex !important;
    flex-direction: column;
    gap: 15px;
  }
  .ListOfBenefitAndLimit-group {
    margin-top: 15px;
  }
  .ModalContainer {
    overflow-x: hidden !important;
    min-width: auto !important;
    max-width: 100% !important;
  }
  .AdditionalItemAngle_degree, .AdditionalItemAngle_profile {
    grid-gap: 10px;
  }
  .ModalTable {
    min-width: auto !important;
    max-width: 100% !important;
  }
  .ModalTable .ModalTable_Row__items {
    padding: 6px !important;
  }
}

