.RecommendationBlock {
  position: absolute;
  display: grid;
  top: -100px;
  left: 15px;
  width: 300px;
  grid-template-columns: 50px 1fr;
  grid-template-rows: min-content min-content;
  height: auto;
  text-align: left;
  z-index: 3;
  gap: 15px;
  padding: 8px;
  flex-direction: column;
  background: white;
  border-radius: 12px;
  opacity: 0;
  transition: opacity 1s ease-in-out;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.2);
}
.RecommendationBlock::before {
  content: "";
  left: 8px;
  top: 8px;
  background: #00C1FD;
  border-radius: 18px;
  width: 2px;
  bottom: 8px;
  position: absolute;
}
.RecommendationBlock__svg {
  justify-self: center;
  align-self: center;
  grid-column: 1;
  grid-row: span 2;
}
.RecommendationBlock__tittle {
  font-size: 16px;
  font-weight: 700;
  grid-column: 2;
  display: block;
  width: 100%;
}
.RecommendationBlock__text {
  grid-column: 2;
  grid-row: 2;
}
.RecommendationBlock__svg, .RecommendationBlock__tittle, .RecommendationBlock__text {
  margin: 0;
  padding: 0;
}

.RecommendationBlock-active {
  opacity: 1;
  transition: opacity 1s ease-in-out;
}

