@import '../../../ui_local/ui_local.scss';
.ButtonModalList {
  font-weight: 500;
  display: grid;
  align-self: end;
  height: 100%;
  width: 100%;
  font-size: 15px;
  font-family: "Playfair Display", serif;
  align-items: center;
  justify-content: end;
  letter-spacing: 2.5px;
  color: $ui_colorText;
  .ButtonModalList_icon {
    margin-right: 25px;
    position: relative;
    width: 50px;
    height: 50px;
    border: 1px solid $ui_colorTextDark;

    .ButtonModalList_icon__span {
      position: absolute;
      right: 0;
      display: inline-block;
      line-height: 13px;
      top: 0;
      padding: 2px 5px;
      color: $ui_colorText;
      text-shadow: 0 0 5px #fff, 0 0 10px #fff;

    }
  }
}
.ModalContainer {
  margin: 15px auto;
  z-index: 10;
  max-width: 1280px;
  min-width: 650px;
  max-height: 90%;
  background: #847C6E;
  color: #E5DCCB;
  overflow-y: scroll;
}
.ModalTable {
  margin: auto;
  z-index: 10;
  width: 100%;
  height: 100%;
  background: #847C6E;
  color: #E5DCCB;

  .ModalTable_Row__items {
    color: #E5DCCB;
  }
}
.ModalDeleteIcon {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-content: center;
  color:#E5DCCB !important;
}